.movies .movie {
    width: 300px;
    background-color: rgb(202, 200, 200);
    margin-bottom: 70px;
    margin-right: 20px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #27282b;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie_data {
    display: flex;
    align-items: flex-start;
}

.movie .movie_summary {
   margin-top: 0;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year {
    margin: 0;
    font-weight: 350;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year {
    margin-right: 10px;
    font-size: 14px;
}